<template lang="html">
  <section class="section1" v-if='section1'>
    <h1 class="section1-title">
      全部商品:
      <i class="icon-right" @click="classification"></i>
    </h1>
    <ul class="section1-list">
      <li v-for="k in section1.list">    
          <img v-lazy="k.imgPath" alt=""  @click="link(k)" :key="k.id">
      </li>
    </ul>
    <img v-lazy="section1.banner" v-if='section1' class="section1-banner">
  </section>
</template>

<script>
import { Lazyload } from "mint-ui";

export default {
  props: ["section1"],
  methods: {
    link(k) {
      this.$router.push({
        path: "/detail",
        query: {
          res: k
        }
      });
    },
    classification() {
      this.$router.push({
        name: "分类页"
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../assets/fz.less";
@import "../../assets/index/style.css";
.section1 {
  .pt();
  .section1-title {
    .bt();
    background-color: #ffffff;
    text-align: center;
    padding: 4vw 0;
    .fz(font-size, 40);
    color: #333;
    position: relative;

    i {
      position: absolute;
      right: 6vw;
      top: 50%;
      .fz(font-size, 36);
      .fz(margin-top,-16);

      &::before {
        color: rgb(159, 159, 159);
      }
    }
  }

  .section1-list {
    display: -ms-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    padding: 0 2vw 4vw 2vw;
    li {
      width: 50%;
      padding: 1vw;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      a,
      img {
        width: 100%;
        display: block;
      }
    }
  }

  .section1-banner {
    display: block;
    width: 100vw;
    img {
      width: 100%;
      height: 24vw;
    }
  }
}
</style>



