<template lang="html">
    <mt-swipe :auto="4000" v-if='swiperData'>
      <mt-swipe-item v-for="k in swiperData" :key="k.id">
    
          <img :src="k.imgPath" @click="link(k)">

      </mt-swipe-item>
    </mt-swipe>

</template>

<script>
export default {
  props: ["swiperData"],
  methods: {
    link(k) {
      this.$router.push({
        path: "/detail",
        query: {
          res: k
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.mint-swipe {
  width: 100%;
  height: 50vw;
  a,
  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}
</style>



